<template>
    <v-card style="border-radius:0px; height: 430px">
        <v-app-bar color="#172d44" dark>
            <div class="d-flex align-center">
                <h3>Editar regra</h3>
            </div>
        </v-app-bar>
        <v-card-text>
            <v-container>
                <v-form ref="editPost" lazy-validation>
                    <v-row align="center">
                        <v-container>
                            <v-col cols="5">
                                <v-select v-model="form.domain" :items="allDomains" :rules="[p => !!p || 'Domínio é obrigatório']" item-text="description" item-value="specific_info" label="Domínio" required></v-select>
                            </v-col>
                        </v-container>
                    </v-row>
                    <v-row align="center" v-for="(v, i) in form.if_clauses" :key="v.id">
                        <v-col cols="1" v-if="i == 0">
                            <v-subheader> se </v-subheader>
                        </v-col>
                        <v-col cols="1" v-else>
                            <v-subheader> </v-subheader>
                        </v-col>
                        <v-col cols="5">
                            <v-select v-model="v.property" :items="properties_to_change_behavior" :rules="[p => !!p || 'Propriedade é obrigatória']" item-text="description_pt" item-value="description_en" label="Propriedade" required></v-select> <!--hide-details single-line ></v-select>-->
                        </v-col>
                        <v-col cols="2">
                            <v-select v-model="v.operator" :items="operators" :rules="[o => !!o || 'Operador é obrigatório']" label="Operador" required></v-select>
                        </v-col>
                        <v-col cols="2">
                            <v-select v-model="v.value" :items="choose_set_of_integers(v.property)" :rules="[v => !!v || 'Valor é obrigatório']" label="Valor" required></v-select>
                        </v-col>
                        <v-col cols="2">
                            <v-select v-model="v.condition" :items="['e', 'ou']" label="e/ou" required @change="add_line(i)"></v-select>
                        </v-col>
                    </v-row>
                    <v-row align="center" v-for="b in form.then_clauses" :key="b.id">
                        <v-col cols="1">
                            <v-subheader> então </v-subheader>
                        </v-col>
                        <v-col cols="3">
                            <v-select v-model="b.action" :items="actions" :rules="[a => !!a || 'Ação é obrigatória']" item-text="description_pt" item-value="description_en" label="ação" required></v-select>
                        </v-col>
                        <v-col cols="5">
                            <v-select v-model="b.property" :items="properties_to_change_behavior" :rules="[p => !!p || 'Propriedade é obrigatória']" item-text="description_pt" item-value="description_en" label="Propriedade" required></v-select>
                        </v-col>
                    </v-row>
                </v-form>
            </v-container>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn class="white--text text-capitalize; mr-2 mb-2" color="#00cc99" @click="cancel()" v-bind="attrs" v-on="on"><v-icon>mdi-door-open</v-icon></v-btn>
                </template>
                <span>Sair</span>
            </v-tooltip>
            <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn class="white--text text-capitalize; mr-2 mb-2" color="#00cc99" @click="save_rule()" v-bind="attrs" v-on="on"><v-icon>fa-check</v-icon></v-btn>
                </template>
                <span>Guardar</span>
            </v-tooltip>
        </v-card-actions>
    </v-card>
</template>

<script>
export default {
    name: 'Form_rule',

    props: ['infoDomain', 'rule_info','greatestId', 'allDomains', 'properties_to_change_behavior', 'actions', 'operators'],
    
    data: function () {
        return {
            //types of integer ranges
            t1: Array(5- 1 + 1).fill().map((_, idx) => 1 + idx).map(String),
            t2: Array(10 - 0 + 1).fill().map((_, idx) => 0 + idx).map(String),
            t3: Array(90 - 0 + 1).fill().map((_, idx) => 0 + idx).map(String),
            t4: Array(100 - 0 + 1).fill().map((_, idx) => 0 + idx).map(String),
            t5: Array(180 - 0 + 1).fill().map((_, idx) => 0 + idx).map(String),
            t6: Array(200 - 0 + 1).fill().map((_, idx) => 0 + idx).map(String),
            form: {
                domain: {},
                if_clauses: [],
                then_clauses: []
            }
        }
    },

    async created(){ await this.fill_form() },

    watch: { async rule_info(){ await this.fill_form() } },

    methods: {
        add_if_clause(){
            this.form.if_clauses.push({
                id: String(this.rule_info.id) + '.' + String(this.form.if_clauses.length + 2),
                property: "",
                operator: "",
                value: "",
                condition: ""
            })
        },
        async add_line(index){
            if( (index == this.form.if_clauses.length-1) && (this.form.if_clauses[index].condition != "") ){ await this.add_if_clause() }
        },
        generate_rule_db_object(){
            var new_rule = {
                "id": String(this.rule_info.id),
                "study_cycle": this.form.domain.study_cycle,
                "scholarity": this.form.domain.scholarity,
                "domain": this.form.domain.desc,
                "if_clauses": this.form.if_clauses,
                "then_clauses": this.form.then_clauses
            }

            return new_rule
        },
        async save_rule(){
            if(this.$refs.editPost.validate()){
                let edited_rule = await this.generate_rule_db_object()

                await this.$http.put('https://leonardo.di.uminho.pt/api/v0/evaluation/rules/' + this.rule_info.id, {
                    'rule': edited_rule
                })
                    .then(() => { this.$emit("updateList") })
                    .catch((error) => { throw(error) })
            }
        },
        choose_set_of_integers(property){
            if( (property == "user_level") || (property == "user_performance") || (property == "user_skill") ) return this.t1
            else if( (property == "total_right_followed_questions") || (property == "total_wrong_followed_questions") ) return this.t2
            else if( (property == "answering_time") ) return this.t3
            else if( (property == "total_right_questions") || (property == "total_wrong_questions") || (property == "hit_rate") || (property == "error_rate") ) return this.t4
            else if( (property == "usage_time") ) return this.t5
            else if( (property == "total_questions") ) return this.t6
            else return []
        },
        fill_form(){
            this.form = {
                domain: {},
                if_clauses: [],
                then_clauses: []
            }

            this.form.domain = this.infoDomain.specific_info

            this.rule_info.if_clauses.forEach(if_clause => {
                this.form.if_clauses.push({
                    id: if_clause.id,
                    property: if_clause.property,
                    operator: if_clause.operator,
                    value: if_clause.value,
                    condition: if_clause.condition
                })
            })
            this.rule_info.then_clauses.forEach(then_clause => {
                this.form.then_clauses.push({
                    id: then_clause.id,
                    action: then_clause.action,
                    property: then_clause.property
                })
            })
        },
        cancel() { this.$emit("closeModal") }
    }
}
</script>