<template>
    <v-card style="border-radius:0px; height: 260px">
        <v-app-bar color="#172d44" dark>
            <div class="d-flex align-center">
                <h3 width="40" v-if="desc == 'Remover regra'"> Aviso </h3>
                <h3 width="40" v-else> {{ desc }} </h3>
            </div>
        </v-app-bar>
        <v-container>
            <div style="height: 120px;">
                <v-row cols="12">
                    <v-col cols="2">
                        <v-row>
                            <v-card class="ml-4 mt-8" color="red" flat height="65px" width="70px" v-if="desc == 'Remover regra'">
                                <div>
                                    <v-icon v-bind:style="'border: 0px solid red; color: white; border-radius:0%; size: 30; height: 65px; width: 70px;'">{{ symbol }}</v-icon>
                                </div>
                            </v-card>
                            <v-card class="ml-4 mt-8" color="#172d44" flat height="65px" width="70px" v-else>
                                <div style="text-align: center;" v-if="(desc != 'Acerca')">
                                    <v-icon v-bind:style="'border: 0px #172d44; color: white; border-radius:0%; size: 30; height: 65px; width: 70px;'">
                                        {{ symbol }}
                                    </v-icon>
                                </div>
                                <div style="text-align: center; margin-top: 18px" v-else>
                                    <i v-bind:class="symbol" style="border: 0px #172d44; color: white; border-radius:0%; font-size: 22px"></i>
                                </div>
                            </v-card>
                        </v-row>
                    </v-col>
                    <v-col cols="10">
                        <v-row>
                            <v-card class="mr-4 mt-6" flat>
                                <vue-custom-scrollbar class="scroll-area" :settings="settings" v-if="desc == 'Remover regra'">
                                    <v-card-text style="font-size: 15px;">
                                        {{ text }}
                                        <p><b>Regra:</b> {{ infoRule.text }}</p>
                                    </v-card-text>
                                </vue-custom-scrollbar>
                                <vue-custom-scrollbar class="scroll-area" :settings="settings" v-else>
                                    <v-card-text style="font-size: 15px;">{{ modal_text }}</v-card-text>
                                </vue-custom-scrollbar>
                            </v-card>
                        </v-row>
                    </v-col>
                </v-row>
            </div>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn class="white--text text-capitalize; mr-2 mt-2" color="red" @click="del_rule()" v-if="(desc == 'Remover regra')" v-bind="attrs" v-on="on"><v-icon>mdi-trash-can-outline</v-icon></v-btn>
                    </template>
                    <span>Remover</span>
                </v-tooltip>
                <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn class="white--text text-capitalize; mt-2" color="#00cc99" @click="cancel()" v-bind="attrs" v-on="on"><v-icon>mdi-door-open</v-icon></v-btn>
                    </template>
                    <span>Sair</span>
                </v-tooltip>
            </v-card-actions>
        </v-container>
    </v-card>
</template>

<script>
  import vueCustomScrollbar from 'vue-custom-scrollbar'
  import "vue-custom-scrollbar/dist/vueScrollbar.css"
  export default {
    name: 'Modal_layout',

    props: ['desc', 'infoRule', 'symbol', 'buttonCode', 'text'],

    components: {
        vueCustomScrollbar
    },

    data: function () {
        return {
            modal_text: "",
            settings: {
                suppressScrollY: false,
                suppressScrollX: false,
                wheelPropagation: false
            }
        }
    },

    async created() {
        if(this.desc != 'Remover regra'){
            await this.$http.get('https://leonardo.di.uminho.pt/api/v0/evaluation/getButtonInfo?buttonCode=' + this.buttonCode)
            .then(result => {
                this.modal_text = result.data
            })
            .catch(error => { throw(error) })
        }
    },

    methods: {
        async del_rule(){
            await this.$http.delete('https://leonardo.di.uminho.pt/api/v0/evaluation/rules/' + this.infoRule.id)
            .then(() => {
                this.$emit("updateList")
            })
            .catch(error => { throw(error) })
        },
        cancel: function() {
            this.$emit("closeModal")
        }
    }
  }
</script>

<style >
.scroll-area {
  position: relative;
  margin: auto;
  width: 470px;
  height: 110px;
}
</style>