<template>
    <v-container fluid v-bind:style="{ backgroundColor: background_color, height: height }">
        <h2 style="font-size: 2.0em; color: lightgrey" class="ml-6">Editor regras de comportamento</h2>
        <v-row align="center" class="ml-3">
          <v-col class="d-flex" cols="12" sm="4">
            <v-select v-model="select" :items="all_domains" :rules="[p => !!p || 'Domínio é obrigatório']" item-text="description" item-value="specific_info" label="<Escolha o domínio>" persistent-hint return-object single-line></v-select>
          </v-col>
        </v-row>
        <v-dialog v-model="form_rule" persistent max-width="800px" scrollable>
            <FormRule :greatestId="greatestId" :allDomains="all_domains" :properties_to_change_behavior="properties_to_change_behavior" :actions="actions" :operators="operators" v-on:closeModal="form_rule = false" v-on:updateList="form_rule = false, get_domains(), get_rules()"/>
        </v-dialog>
        <v-dialog v-model="edit_rule" persistent max-width="800px" scrollable>
            <EditRule :infoDomain="select" :rule_info="rule_to_edit" :greatestId="greatestId" :allDomains="all_domains" :properties_to_change_behavior="properties_to_change_behavior" :actions="actions" :operators="operators" v-on:closeModal="edit_rule = false" v-on:updateList="edit_rule = false, get_domains(), get_rules()"/>
        </v-dialog>
        <v-dialog v-model="delete_rule" persistent max-width="600px">
            <ModalLayout :infoRule="current_rule" :desc="'Remover regra'" :symbol="'mdi-alert'" :text="'Tem a certeza que pretende remover esta regra? O resultado desta ação é irreversível.'" v-on:closeModal="delete_rule = false" v-on:updateList="delete_rule = false, get_domains(), get_rules()"/>
        </v-dialog>
        <v-container v-if="dialog_statistics" class="d-flex justify-center" style="position: absolute; z-index: 9">
            <vue-draggable-resizable style="border: 0px solid black; width: 600px" :resizable="false" :disable-user-select="false">
                <ModalLayout :desc="'Estatísticas'" :symbol="'mdi-chart-bar'" :buttonCode="'7.4'" v-on:closeModal="dialog_statistics = false"/>
            </vue-draggable-resizable>
        </v-container>
        <v-container v-if="dialog_rules" class="d-flex justify-center" style="position: absolute; z-index: 9">
            <vue-draggable-resizable style="border: 0px solid black; width: 600px" :resizable="false" :disable-user-select="false">
                <ModalLayout :desc="'Regras'" :symbol="'mdi-ruler'" :buttonCode="'6.4'" v-on:closeModal="dialog_rules = false"/>
            </vue-draggable-resizable>
        </v-container>
        <v-container v-if="dialog_help" class="d-flex justify-center" style="position: absolute; z-index: 9">
            <vue-draggable-resizable style="border: 0px solid black; width: 600px" :resizable="false" :disable-user-select="false">
                <ModalLayout :desc="'Ajuda'" :symbol="'mdi-lightbulb'" :buttonCode="'1.5'" v-on:closeModal="dialog_help = false"/>
            </vue-draggable-resizable>
        </v-container>
        <v-container v-if="dialog_about" class="d-flex justify-center" style="position: absolute; z-index: 9">
            <vue-draggable-resizable style="border: 0px solid black; width: 600px" :resizable="false" :disable-user-select="false">
                <ModalLayout :desc="'Acerca'" :symbol="'fa fa-info-circle'" :buttonCode="'2.6'" v-on:closeModal="dialog_about = false"/>
            </vue-draggable-resizable>
        </v-container>
        <v-expand-transition>
            <v-card v-show="showRules" class="ml-6 mr-6">
                <v-alert color="warning" icon="warning" v-if="rules_with_sentences.length == 0">
                    Não existem regras associadas a este domínio
                </v-alert>
                <v-card-text v-else>
                    <v-card-title style="font-size: 1.6em" class="mt-2 ml-2">Lista de Regras</v-card-title>
                    <v-card-subtitle style="font-size: 1.2em" class="mt-2 ml-2">{{ select.description }}</v-card-subtitle>
                    <v-list class="ml-4">
                        <v-list-item v-for="r in visible_rules" :key="r.id" class="mt-n3">
                            <v-list-item-avatar>
                                (r{{ r.id }})
                            </v-list-item-avatar>
                            <v-list-item-content>
                                <v-list-item-subtitle> {{ r.text }} </v-list-item-subtitle>
                            </v-list-item-content>
                            <v-list-item-action>
                                <v-tooltip top>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn x-small class="ma-1" style="width: 2px; height: 28px" color="light-blue" dark @click="edit_current_rule(r)" v-bind="attrs" v-on="on">
                                            <v-icon>mdi-pencil</v-icon>
                                        </v-btn>
                                    </template>
                                    <span>Editar</span>
                                </v-tooltip>
                            </v-list-item-action>
                            <v-list-item-action>
                                <v-tooltip top>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn x-small class="ma-0" style="width: 2px; height: 28px" color="red" dark @click="current_rule = r, delete_rule = true" v-bind="attrs" v-on="on">
                                            <v-icon>mdi-trash-can-outline</v-icon>
                                        </v-btn>
                                    </template>
                                    <span>Remover</span>
                                </v-tooltip>
                            </v-list-item-action>
                        </v-list-item>
                    </v-list>
                    <v-row class="ml-4 mt-4">
                        <v-pagination v-model="page_number" :length="Math.ceil(rules_with_sentences.length / items_per_page)" prev-icon="mdi-menu-left" next-icon="mdi-menu-right"></v-pagination>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-expand-transition>
        <v-row class="ma-6" cols="12" flat v-bind:style="{ backgroundColor: background_color }" style="display: flex; justify-content: space-between">
            <v-col cols="6">
                <v-row style="height: 150px; float: left">
                    <v-card class="ml-0" flat v-bind:style="{ backgroundColor: background_color }">
                        <div>
                            <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn class="white--text text-capitalize" color="primary" @click="form_rule = true" v-bind="attrs" v-on="on">
                                        <v-icon dark>mdi-plus</v-icon>
                                    </v-btn>
                                </template>
                                <span>Nova</span>
                            </v-tooltip>
                        </div>
                        <div class="mt-2">
                            <v-tooltip bottom>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize" color="#00cc99" @click="reload_component()" v-bind="attrs" v-on="on">
                                    <v-icon>mdi-broom</v-icon>
                                </v-btn>
                                </template>
                                <span>Limpar</span>
                            </v-tooltip>
                        </div>
                    </v-card>
                    <v-card class="ml-2" flat v-bind:style="{ backgroundColor: background_color }">
                        <div>
                            <v-btn text disabled></v-btn>
                        </div>
                        <div class="mt-2">
                            <v-tooltip bottom>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize"  color="#00cc99" v-bind="attrs" v-on="on">
                                    <v-icon>mdi-flag-checkered</v-icon>
                                </v-btn>
                                </template>
                                <span>Terminar</span>
                            </v-tooltip>
                        </div>
                        <div class="mt-2">
                            <v-btn text disabled></v-btn>
                        </div>
                    </v-card>
                </v-row>
            </v-col>
            <v-col cols="6">
                <v-row style="height: 150px; float: right">
                    <v-card class="mr-2" flat v-bind:style="{ backgroundColor: background_color }">
                        <div >
                            <v-btn text disabled></v-btn>
                        </div>
                        <div class="mt-2">
                            <v-btn text disabled></v-btn>
                        </div>
                        <div class="mt-2">
                            <v-tooltip bottom>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize" color="#172d44" v-bind="attrs" v-on="on" @click="dialog_rules = !dialog_rules">
                                    <v-icon>mdi-ruler</v-icon>
                                </v-btn>
                                </template>
                                <span>Regras</span>
                            </v-tooltip>
                        </div>
                    </v-card>
                    <v-card class="mr-2" flat v-bind:style="{ backgroundColor: background_color }">
                        <div>
                            <v-btn text disabled></v-btn>
                        </div>
                        <div class="mt-2">
                            <v-tooltip left>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize" color="#00cc99" v-bind="attrs" v-on="on">
                                    <v-icon>mdi-volume-high</v-icon>
                                </v-btn>
                                </template>
                                <span>Voz</span>
                            </v-tooltip>
                        </div>
                        <div class="mt-2">
                            <v-tooltip bottom>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize" color="#172d44" v-bind="attrs" v-on="on" @click="dialog_help = !dialog_help">
                                    <v-icon>mdi-lightbulb</v-icon>
                                </v-btn>
                                </template>
                                <span>Ajuda</span>
                            </v-tooltip>
                        </div>
                    </v-card>
                    <v-card class="mr-0" flat v-bind:style="{ backgroundColor: background_color }">
                        <div>
                            <v-tooltip left>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize" color="#172d44" v-bind="attrs" v-on="on" @click="dialog_statistics = !dialog_statistics">
                                    <v-icon>mdi-chart-bar</v-icon>
                                </v-btn>
                                </template>
                                <span>Estatísticas</span>
                            </v-tooltip>
                        </div>
                        <div class="mt-2">
                            <v-btn class="white--text text-capitalize" color="#00cc99">
                                <v-tooltip right>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-avatar size="32" v-bind="attrs" v-on="on">
                                            <v-img :src="require('../assets/raccoon.png')"></v-img>
                                        </v-avatar>
                                    </template>
                                    <span>Leonardo</span>
                                </v-tooltip>
                            </v-btn>
                        </div>
                        <div class="mt-2">
                            <v-tooltip bottom>
                                <template v-slot:activator="{ on, attrs }">
                                <v-btn class="white--text text-capitalize" color="#172d44" v-bind="attrs" v-on="on" @click="dialog_about = !dialog_about">
                                    <i class="fa fa-info-circle" style="font-size: 22px"></i>
                                </v-btn>
                                </template>
                                <span>Acerca</span>
                            </v-tooltip> 
                        </div>
                    </v-card>
                </v-row>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import FormRule from './Form_rule'
    import EditRule from './Edit_rule'
    import ModalLayout from './Modal_layout';

    export default {
        name: 'Rules',

        components: {
            FormRule,
            EditRule,
            ModalLayout
        },

        data: function () {
            return {
                background_color: "#F7F7F7",
                height: "1000px",
                operators: ['=', '>', '<', '<>', '>=', '<='],
                properties_to_change_behavior: [
                    {
                        description_pt: "nível atual",
                        description_en: "user_level",
                    },
                    {
                        description_pt: "desempenho",
                        description_en: "user_performance",
                    },
                    {
                        description_pt: "perícia",
                        description_en: "user_skill",
                    },
                    {
                        description_pt: "número de questões",
                        description_en: "total_questions",
                    },
                    {
                        description_pt: "número de questões corretas",
                        description_en: "total_right_questions",
                    },
                    {
                        description_pt: "número de questões erradas",
                        description_en: "total_wrong_questions",
                    },
                    {
                        description_pt: "taxa de acerto",
                        description_en: "hit_rate",
                    },
                    {
                        description_pt: "taxa de erro",
                        description_en: "error_rate",
                    },
                    {
                        description_pt: "tempo de utilização",
                        description_en: "usage_time",
                    },
                    {
                        description_pt: "tempo de resposta",
                        description_en: "answering_time",
                    },
                    {
                        description_pt: "número de questões corretas seguidas",
                        description_en: "total_right_followed_questions",
                    },
                    {
                        description_pt: "número de questões erradas seguidas",
                        description_en: "total_wrong_followed_questions",
                    },
                ],
                actions: [
                    {
                        description_pt: "incrementar",
                        description_en: "increment"
                    },
                    {
                        description_pt: "decrementar",
                        description_en: "decrement"
                    }
                ],
                select: {
                    specific_info: {
                        desc: "",
                        study_cycle: "",
                        scholarity: ""
                    }
                },
                rule_to_edit: {},
                rules_by_domain: [],
                rules_with_sentences: [],
                rules_associated_with_selected_domain: [],
                all_domains: [],
                current_rule: {},
                form_rule: false,
                edit_rule: false,
                delete_rule: false,
                value_desc: "",
                showRules: false,
                dialog_remove: false, dialog_statistics: false, dialog_rules: false, dialog_help: false, dialog_about: false,
                numberOfRules: 0,
                greatestId: 0,
                page_number: 1,
                items_per_page: 5
            }
        },
        async created(){
            await this.get_domains()
            await this.get_rules()
        },
        computed: {
            visible_rules() {
                return this.rules_with_sentences.slice((this.page_number - 1)* this.items_per_page, this.page_number* this.items_per_page) //this.set_of_visible_elements
            }
        },
        watch: {
            select: function() {
                if( (this.select.description == "") && (this.select.specific_info.desc == "") && (this.select.specific_info.study_cycle == "") && (this.select.specific_info.scholarity == "") ){
                    this.showRules = false
                }
                else {
                    var flag = 0
                    this.rules_by_domain.forEach(element => {
                        if( (element.domain.desc == this.select.specific_info.desc) && (element.domain.scholarity == this.select.specific_info.scholarity) && (element.domain.study_cycle == this.select.specific_info.study_cycle) ){
                            this.rules_associated_with_selected_domain = []
                            this.rules_associated_with_selected_domain = element.rules
                            this.rules_with_sentences = []
                            this.rules_with_sentences = this.rule_clauses_to_sentences(element.rules)

                            this.rules_associated_with_selected_domain.sort(function(a,b){
                                if(parseInt(a.id) < parseInt(b.id)) return -1;
                                if(parseInt(b.id) < parseInt(a.id)) return 1;
                            })

                            this.rules_with_sentences.sort(function(a,b){
                                if(parseInt(a.id) < parseInt(b.id)) return -1;
                                if(parseInt(b.id) < parseInt(a.id)) return 1;
                            })

                            var number_of_possible_pages = Math.ceil(this.rules_with_sentences.length / this.items_per_page)
                            
                            if(this.page_number > number_of_possible_pages){
                                this.page_number -= 1
                            }

                            flag = 1
                        }
                    })

                    if(flag == 0){
                        this.rules_associated_with_selected_domain = []
                        this.rules_with_sentences = []
                    }

                    if(this.showRules == false) this.showRules = true
                }
            },
        },
        methods: {
            async get_domains(){
                await this.$http.get('https://leonardo.di.uminho.pt/api/v0/evaluation/getDomains')
                .then(response => {
                    var domains_in_db = eval(response.data)

                    this.all_domains = []
                    domains_in_db.forEach(domain_in_db => {
                        this.all_domains.push({
                            description: domain_in_db.description,
                            specific_info: {
                                id: domain_in_db.id,
                                desc: domain_in_db.description,
                                scholarity: domain_in_db.scholarity,
                                study_cycle: domain_in_db.study_cycle
                            }
                        })
                    })
                })
                .catch(error => { throw(error) })
            },
            async get_rules(){
                await this.$http.get('https://leonardo.di.uminho.pt/api/v0/evaluation/rules')
                .then(response => {
                    var r = eval(response.data)

                    this.greatestId = 0

                    this.rules_by_domain = []
                    r.forEach(element => {
                        var element_rules = element.rules

                        element_rules.sort(function(a,b){
                            if(parseInt(a.id) < parseInt(b.id)) return -1;
                            if(parseInt(b.id) < parseInt(a.id)) return 1;
                        })

                        this.rules_by_domain.push({
                            domain: {
                                desc: element._id.domain,
                                scholarity: element._id.scholarity,
                                study_cycle: element._id.study_cycle
                            },
                            rules: element_rules
                        })
                    })

                    var flag = 0
                    this.rules_by_domain.forEach(element => {
                        element.rules.forEach(rule => {
                            if( parseInt(rule.id) > this.greatestId ){
                                this.greatestId = parseInt(rule.id)
                            }
                        })
                        
                        if( (element.domain.desc == this.select.specific_info.desc) && (element.domain.scholarity == this.select.specific_info.scholarity) && (element.domain.study_cycle == this.select.specific_info.study_cycle) ){
                            this.rules_associated_with_selected_domain = []
                            this.rules_associated_with_selected_domain = element.rules
                            this.rules_with_sentences = []
                            this.rules_with_sentences = this.rule_clauses_to_sentences(element.rules)

                            this.rules_associated_with_selected_domain.sort(function(a,b){
                                if(parseInt(a.id) < parseInt(b.id)) return -1;
                                if(parseInt(b.id) < parseInt(a.id)) return 1;
                            })

                            this.rules_with_sentences.sort(function(a,b){
                                if(parseInt(a.id) < parseInt(b.id)) return -1;
                                if(parseInt(b.id) < parseInt(a.id)) return 1;
                            })

                            var number_of_possible_pages = Math.ceil(this.rules_with_sentences.length / this.items_per_page)
                            
                            if(this.page_number > number_of_possible_pages){
                                this.page_number -= 1
                            }

                            flag = 1
                        }
                    })

                    if(flag == 0){
                        this.rules_associated_with_selected_domain = []
                        this.rules_with_sentences = []
                    }
                })
                .catch(error => { throw(error) })
            },
            edit_current_rule(rule){
                this.rule_to_edit = this.rules_associated_with_selected_domain.filter(obj => { return obj.id === rule.id })[0]

                this.edit_rule = true
            },
            rule_clauses_to_sentences(rules){
                var edited_set_of_rules = []

                rules.forEach(rule => {
                    var clauses_to_sentences = ""

                    var i = 0
                    rule.if_clauses.forEach(if_clause => {
                        if(i == 0){ clauses_to_sentences = clauses_to_sentences.concat("se ") }
                        if(i > 0) clauses_to_sentences = clauses_to_sentences.concat(" ")

                        var if_clause_property = this.properties_to_change_behavior.filter(obj => { return obj.description_en === if_clause.property })[0].description_pt

                        clauses_to_sentences = clauses_to_sentences.concat(if_clause_property, " ", if_clause.operator, " ", if_clause.value, " ", if_clause.condition, " ")
                        
                        i = i + 1
                    })

                    rule.then_clauses.forEach(then_clause => {
                        var j = 0

                        if(j == 0) clauses_to_sentences = clauses_to_sentences.concat("então ")

                        var then_clause_action = this.actions.filter(obj => { return obj.description_en === then_clause.action})[0].description_pt
                        var then_clause_property = this.properties_to_change_behavior.filter(obj => { return obj.description_en === then_clause.property })[0].description_pt

                        clauses_to_sentences = clauses_to_sentences.concat(then_clause_action, " ", then_clause_property)
                    })

                    edited_set_of_rules.push({
                        id: rule.id,
                        text: clauses_to_sentences
                    })
                })
                return edited_set_of_rules
            },
            reload_component(){
                this.select = {
                    description: "",
                    specific_info: {
                        desc: "",
                        study_cycle: "",
                        scholarity: ""
                    }
                }
            }
        }
    }
</script>
